import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import {
  Accordion,
  AccordionItem,
  Box,
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputTooltipProps,
  InputType,
  WindowComponent,
  withTooltip,
} from 'summer';
import { CLASS_NAME, EmpresaMLF } from '../../../../models/dtos/mlf/empresaMLF';
import { GO } from '../../../../models/enumerated/ger/ufEnum';
import { CREDITO_PRESUMIDO_TIPO_CALCULO_ENUM_VALUES } from '../../../../models/enumerated/mlf/creditoPresumidoTipoCalculoEnum';
import { ME_EPP_ENUM_VALUES } from '../../../../models/enumerated/mlf/meEppEnum';
import { SIMPLESNACIONAL } from '../../../../models/enumerated/mlf/tipoTributacaoEnum';
import EmpresaIS from '../../../components/ger/empresaIS/EmpresaIS';
import ConfigCOFINSIS from '../../../components/mlf/configCofinsIS/ConfigCOFINSIS';
import ConfigCSLLIS from '../../../components/mlf/configCSLLIS/ConfigCSLLIS';
import ConfigICMSDAIS from '../../../components/mlf/configICMSDAIS/ConfigICMSDAIS';
import ConfigICMSDIFALIS from '../../../components/mlf/configICMSDIFALIS/ConfigICMSDIFALIS';
import ConfigICMSIS from '../../../components/mlf/configICMSIS/ConfigICMSIS';
import ConfigICMSSTIS from '../../../components/mlf/configICMSSTIS/ConfigICMSSTIS';
import ConfigIPIIS from '../../../components/mlf/configIPIIS/ConfigIPIIS';
import ConfigIRPJIS from '../../../components/mlf/configIRPJIS/ConfigIRPJIS';
import ConfigIRRFIS from '../../../components/mlf/configIRRFIS/ConfigIRRFIS';
import ConfigPISIS from '../../../components/mlf/configPISIS/ConfigPISIS';
import ConfigRetencaoFonteIS from '../../../components/mlf/configRetencaoFonteIS/ConfigRetencaoFonteIS';
import ConfigSimplesNacionalIS from '../../../components/mlf/configSimplesNacionalIS/ConfigSimplesNacionalIS';

interface EmpresaMLFProps extends EditViewProps<EmpresaMLF> {}

const ConfigImpostoEmpresaEditView: React.FC<EmpresaMLFProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<EmpresaMLF>({ defaultValues: selectedItem, mode: 'onBlur' });
  const [isSimplesNacional, setIsSimplesNacional] = useState(false);
  const [isDIFALGO, setIsDIFALGO] = useState(false);
  const [isAliquotaCreditoPresumido, setIsAliquotaCreditoPresumido] = useState(true);

  useEffect(
    () => {
      validarSimplesNacional();
      validarDIFALGO();
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validarAliquotaCreditoPresumido = () => {
    if (formProps.getValues().creditoPresumido) {
      setIsAliquotaCreditoPresumido(false);
    } else {
      setIsAliquotaCreditoPresumido(true);
    }
  };

  const validarSimplesNacional = () => {
    if (formProps.getValues().empresa.tipoTributacao === SIMPLESNACIONAL.value) {
      setIsSimplesNacional(true);
    } else {
      setIsSimplesNacional(false);
    }
  };

  const validarDIFALGO = () => {
    let result = false;
    if (formProps.getValues().empresa.enderecos.length >= 0) {
      formProps.getValues().empresa.enderecos.find(it => {
        if (it.tipoEnderecoPrincipal === true && it.uf.sigla === GO.value) {
          result = true;
        }
        return result;
      });
      setIsDIFALGO(result);
    } else {
      setIsDIFALGO(false);
    }
  };

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'auto',
    };
  };

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().gerarRetencaoParaSaidas && !formProps.getValues().configRetencaoFonte) {
      erros.push(
        'Se a opção Gerar Retenção na Fonte para Notas Fiscais de Saída estiver habilitada, uma configuração de Retenção na Fonte deve ser escolhida !.'
      );
    }

    return erros;
  };

  const isEmpresaSimplesNacional = isSimplesNacional ? (
    <AccordionItem title="Configurações de Imposto - Simples Nacional">
      <Row>
        <Col>
          <ConfigSimplesNacionalIS formProps={{ ...formProps, validation: { required: true } }} id="configSimplesNacional" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={formProps}
            id="meEpp"
            isPrependLabel={true}
            label="Enquadramento"
            placeholder="Selecione o tipo de cálculo"
            inputDropDownProps={{
              optionsProvider: ME_EPP_ENUM_VALUES,
              selectProps: { isClearable: true },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="aliquotaPadraoISS"
            label="Alíquota padrão de ISS"
            formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
            isPrependLabel={true}
            type={InputType.NUMBER}
            placeholder="Informe a Alíquota padrão de ISS"
          />
        </Col>
        <Col className="mt-2">
          <CustomInput
            type="switch"
            label="Calcular Diferença de Alíquota do ativo"
            name="calcularDifAliquota"
            id="idcalcularDifAliquota"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(propToolTip('Calcula diferença de alíquota do ativo.(1/48 do campo 069 do DAPI e Apuração de ICMS)'), 'calcularDifAliquota')}
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Gerar Provisão/Reconhecimento de Despesas de impostos para o último dia da referência do imposto"
            name="usarDataProvisao"
            id="idusarDataProvisao"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(
            propToolTip(
              'Caso esta opção seja marcada será gerada Provisão/Reconhecimento da Despesas de impostos para o último dia da referência do imposto, caso contrário a data da geração será a mesma do vencimento do imposto.'
            ),
            'usarDataProvisao'
          )}
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Regime Caixa"
            name="regimeCaixa"
            id="idregimeCaixa"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(
            propToolTip(
              'Calcula os Impostos Federais em Regime de Caixa. Utiliza os valores do Contas a Receber como base para o cálculo dos Impostos Federais'
            ),
            'regimeCaixa'
          )}
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Acumular Faturamento das Filiais"
            name="acumulaFaturamento"
            id="idacumulaFaturamento"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(propToolTip('Acumular Faturamento das Filias na Matriz'), 'acumulaFaturamento')}
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Regime especial de substituição tributária"
            name="regimeEspecialST"
            id="idregimeEspecialST"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(propToolTip('Concessão de Regime Especial, nos termos do artigo 9º, Anexo XV do RICMS/MG'), 'regimeEspecialST')}
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Aplicar Redução de ICMS de acordo com faturamento (PGDAS)"
            name="aplicarReducaoIcmsEstado"
            id="idaplicarReducaoIcmsEstado"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(
            propToolTip(
              'Define alíquota para redução ou isenção do ICMS no cálculo do Simples Nacional realizado pelo PGDAS de acordo com a legislação vigente para tal em cada estado.'
            ),
            'aplicarReducaoIcmsEstado'
          )}
        </Col>
      </Row>
    </AccordionItem>
  ) : (
    <AccordionItem title="Configurações de Impostos - Federais">
      <Row>
        <Col>
          <ConfigIPIIS formProps={formProps} id="configIPI" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <ConfigCOFINSIS formProps={formProps} id="configCOFINS" isPrependLabel={true} />
        </Col>
        <Col>
          <ConfigCSLLIS formProps={formProps} id="configCSLL" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <ConfigIRPJIS formProps={formProps} id="configIRPJ" isPrependLabel={true} />
        </Col>
        <Col>
          <ConfigPISIS formProps={formProps} id="configPIS" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <ConfigRetencaoFonteIS formProps={formProps} id="configRetencaoFonte" isPrependLabel={true} />
        </Col>
        <Col>
          <ConfigIRRFIS formProps={formProps} id="configIRRF" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="aliquotaPadraoISS"
            label="Alíquota Padrão de ISS"
            formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
            isPrependLabel={true}
            type={InputType.NUMBER}
            placeholder="Informe a Alíquota Padrão de ISS"
          />
        </Col>
        <Col>
          <ConfigIRRFIS
            formProps={formProps}
            id="configIRRFAluguel"
            label="IR R.F. (Aluguéis)"
            placeholder="Informe o IR R.F. (Aluguéis)"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Gerar retenção na fonte para notas fiscais (NF-e) de saída."
            name="gerarRetencaoParaSaidas"
            id="idgerarRetencaoParaSaidas"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(propToolTip('Gerar Retenção na Fonte para Notas Fiscais (NF-e) de Saída.'), 'gerarRetencaoParaSaidas')}
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Gerar Provisão/Reconhecimento da despesas de impostos para o último dia da referencia do imposto"
            name="usarDataProvisao"
            id="idusarDataProvisao"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(
            propToolTip(
              'Caso esta opção seja marcada será gerada Provisão/Reconhecimento da Despesas de impostos para o último dia da referência do imposto, caso contrário a data da geração será a mesma do vencimento do imposto.'
            ),
            'usarDataProvisao'
          )}
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Regime de Caixa"
            name="regimeCaixa"
            id="idregimeCaixa"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(
            propToolTip(
              'Calcula os Impostos Federais em Regime de Caixa. Utiliza os valores do Contas a Receber como base para o cálculo dos Impostos Federais'
            ),
            'regimeCaixa'
          )}
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Acumular faturamento das filiais na matriz"
            name="acumulaFaturamento"
            id="idacumulaFaturamento"
            innerRef={formProps.register}
            style={{ marginBottom: '16px' }}
          />
          {withTooltip(propToolTip('Acumular Faturamento das Filias na Matriz'), 'acumulaFaturamento')}
        </Col>
      </Row>
    </AccordionItem>
  );

  return (
    <WindowComponent title="Configurações de Impostos">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <EmpresaIS
              formProps={formProps}
              label="Empresa"
              placeholder="Selecione a empresa"
              id="empresa"
              disabled={true}
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
            />
          </Col>
        </Row>
        <Accordion formProps={formProps}>
          {isEmpresaSimplesNacional}
          <AccordionItem title="Configuração de Impostos - Estaduais">
            {!isSimplesNacional && (
              <Box title="Crédito Presumido">
                <Row>
                  <Col className="mt-2">
                    <CustomInput
                      type="switch"
                      label="Calcular"
                      name="creditoPresumido"
                      onChange={validarAliquotaCreditoPresumido}
                      id="idcreditoPresumido"
                      innerRef={formProps.register}
                      style={{ marginBottom: '16px' }}
                    />
                    {withTooltip(
                      propToolTip('Substitui todos os créditos por um determinado percentual relativo ao imposto debitado por ocasião das saídas.'),
                      'creditoPresumido'
                    )}
                  </Col>
                  <Col>
                    <InputGroupInline
                      id="creditoPresumidoAliquota"
                      label="Alíquota"
                      formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
                      isPrependLabel={true}
                      type={InputType.NUMBER}
                      disabled={isAliquotaCreditoPresumido}
                      placeholder="Informe a Alíquota"
                    />
                  </Col>
                  <Col>
                    <InputGroupInline
                      formProps={formProps}
                      id="creditoPresumidoTipoCalculo"
                      isPrependLabel={true}
                      label="Tipo Cálculo"
                      placeholder="Selecione o tipo de cálculo"
                      inputDropDownProps={{
                        optionsProvider: CREDITO_PRESUMIDO_TIPO_CALCULO_ENUM_VALUES,
                        selectProps: { isClearable: true },
                      }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
              </Box>
            )}
            <Row>
              <Col>
                <ConfigICMSIS formProps={formProps} id="configICMS" isPrependLabel={true} />
              </Col>
            </Row>
            <Row>
              <Col>
                <ConfigICMSSTIS
                  formProps={formProps}
                  id="configICMSST"
                  label="Configuração do ICMS-ST a recolher de entrada"
                  placeholder="Selecione a configuração do ICMS-ST a recolher de entrada"
                  isPrependLabel={true}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <ConfigICMSSTIS
                  formProps={formProps}
                  id="configICMSSTRetSai"
                  label="Configuração do ICMS-ST retido de saída"
                  placeholder="Selecione a configuração do ICMS-ST retido de saída"
                  isPrependLabel={true}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <ConfigICMSDAIS
                  formProps={formProps}
                  id="configICMSDA"
                  label="Configuração do ICMS-DA (Aquisição)"
                  placeholder="Selecione a configuração do ICMS-DA (Aquisição)"
                  isPrependLabel={true}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <ConfigICMSDAIS
                  formProps={formProps}
                  id="configICMSDAUsoConsumo"
                  label="Configuração do ICMS-DA (Uso, Consumo ou Ativo permanente)"
                  placeholder="Selecione a configuração do ICMS-DA (Uso, Consumo ou Ativo permanente)"
                  isPrependLabel={true}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <ConfigICMSDIFALIS
                  formProps={formProps}
                  id="configICMSDifal"
                  label="Configuração do ICMS-DIFAL"
                  placeholder="Selecione a configuração do ICMS-DIFAL"
                  isPrependLabel={true}
                />
              </Col>
            </Row>
            {isDIFALGO && (
              <Row>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Possui benefício fiscal de acordo com o inciso VIII do art. 8º do Anexo IX – RCTE"
                    name="possuiBeneficioRCTEGO"
                    id="idpossuiBeneficioRCTEGO"
                    innerRef={formProps.register}
                    style={{ marginBottom: '16px' }}
                  />
                  {withTooltip(
                    propToolTip(
                      'Deve marcar esta opção o contribuinte optou pela utilização do benefício fiscal referido no inciso I do art. 3º da lei 9.162/2018. Esta opção irá impactar no cálculo do DIFAL.'
                    ),
                    'possuiBeneficioRCTEGO'
                  )}
                </Col>
              </Row>
            )}
            {!isSimplesNacional && (
              <Row>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Calcular diferença de alíquota do alvo"
                    name="calcularDifAliquota"
                    id="idcalcularDifAliquota"
                    innerRef={formProps.register}
                    style={{ marginBottom: '16px' }}
                  />
                  {withTooltip(
                    propToolTip('Calcula diferença de alíquota do ativo.(1/48 do campo 069 do DAPI e Apuração de ICMS)'),
                    'calcularDifAliquota'
                  )}
                </Col>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Regime especial de substituição tributária"
                    name="regimeEspecialST"
                    id="idregimeEspecialST"
                    innerRef={formProps.register}
                    style={{ marginBottom: '16px' }}
                  />
                  {withTooltip(propToolTip('Concessão de Regime Especial, nos termos do artigo 9º, Anexo XV do RICMS/MG'), 'regimeEspecialST')}
                </Col>
              </Row>
            )}
          </AccordionItem>
        </Accordion>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigImpostoEmpresaEditView;
