import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mlf/configIrpj';

const ConfigIRPJIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
          {
            dataField: 'codigoReceita',
            text: 'Código da Receita',
          },
          {
            dataField: 'periodoApuracao',
            text: 'Período de Apuração',
          },
          {
            dataField: 'diaVencimento',
            text: 'Dia de Vencimento',
          },
          {
            dataField: 'aliquota',
            text: 'Alíquota',
          },
          {
            dataField: 'recolhimentoMinimo',
            text: 'Recolhimento Mínimo',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'ConfigIrpjSearchView',
      }}
      label={props.label ? props.label : 'IRPJ'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o IRPJ'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default ConfigIRPJIS;
